}
#cardh{
  font-size: 18px;
  background-color: lightcoral;
  margin: 0px;
  padding: 20px 0px;
}
#card{
  padding: 0px;
  font-size: 15px;
  margin: 0px 15px;
  margin-bottom: 30px;
  text-align: center;
  width: 90%;
  border: 1px solid black;
}
#cardb{
  margin: 0px;
  padding: 0px;
}

#map{
  margin-left: 10px;
  flex-basis: 300%;
}
.querybox{
  background: linear-gradient(to right, #fc4958, #e85d04);
  /* margin-left: 10px; */
  display: flex;
  flex-direction: row;
  grid-gap: 100px;
  gap: 100px;
}

#selectbox{
  
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 0px;
    
}
#selectbox select,button{
  font-size: 14px;
  width: 150px;
}
.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 1 / 2 / 2 / 3; }
.div3 { grid-area: 1 / 3 / 2 / 4; }
.div4 { grid-area: 2 / 1 / 3 / 2; }
.div5 { grid-area: 2 / 2 / 3 / 3; }
.div6 { grid-area: 2 / 3 / 3 / 4; }
.div7 { grid-area: 3 / 2 / 4 / 3; margin-bottom: 10px;}


.coordbox {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 15px;
  grid-row-gap: 0px;
  }
.coordbox input,button{
  font-size: 14px;
  width: 150px;
}

#rescard {
  z-index: -5;
  width: 90%;
  margin-left: 15px;
  font-size: 18px;
  margin-bottom: 10px;
  text-align: center;
}
.tab-pane .row{
  margin-top: 15px;
}
.img {
  transition: 0.8s ease-in-out;
}
.img:hover{
    transform: scale(1.2);
}
.modal-backdrop
{
    opacity:0.5 !important;
}
.profile-box{
  padding: 30px;
}
.button {
    position: relative;
    background-color: black;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    color: #FFFFFF;
    width: 150px;
    text-align: center; /* Safari */
    transition-duration: 0.4s;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
  }
  
  .button:after {
    content: "";
    background: white;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px!important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
  }
  
  .button:active:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s
  }
